export default function (Vue) {
  Vue.transition('zoom', {
    enterClass: 'zoomIn',
    leaveClass: 'zoomOut',
  });
  Vue.transition('fade', {
    enterClass: 'fadeIn',
    leaveClass: 'fadeOut',
  });
  Vue.transition('page', {
    enterClass: 'slideInRight',
    leaveClass: 'slideOutLeft',
  });
}



