import pool from '../components/pages/pool.vue';
import champion from '../components/pages/champion.vue';
import checkpoint from '../components/pages/checkpoint.vue';
import user from '../components/pages/user.vue';

export default {
  '/': {
    name: 'home',
    component: pool,
    subRoutes: {
      '/checkpoint': {
        name: 'checkpoint',
        component: checkpoint,
      },
    },
  },
  '/user': {
    name: 'user',
    component: user,
  },
  '/:name': {
    name: 'champion',
    component: champion,
    subRoutes: {
      '/checkpoint': {
        name: 'checkpointChampion',
        component: checkpoint,
      },
    },
  },
};



