import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const state = {
  loginStatus: false,
  loading: false,
  loginFail: false,
  redirect: '',
  route: '',
};

// Create an object storing various mutations. We will write the mutation
const mutations = {
  TOGGLE_LOGIN_STATUS(mState, mutation) {
    mState.loginStatus = mutation;
  },
  TOGGLE_LOADING(mState) {
    mState.loading = !mState.loading;
  },
  LOGIN_FAIL(mState, mutation) {
    mState.loginFail = mutation;
  },
  UPDATE_REDIRECT(mState, mutation) {
    mState.redirect = mutation;
  },
  UPDATE_ROUTE(mState, mutation) {
    mState.route = mutation;
  },
};

// Combine the initial state and the mutations to create a Vuex store.
// This store can be linked to our app.
export default new Vuex.Store({
  state,
  mutations,
});



