export function getLoginStatus(state) {
  return state.loginStatus;
}

export function getLoading(state) {
  return state.loading;
}

export function getLoginFail(state) {
  return state.loginFail;
}

export function getRedirect(state) {
  return state.redirect;
}

export function getRoute(state) {
  return state.route;
}



