<template>
    <div class="champion-view" :style='{backgroundImage: `url(${dDragonBackgroundImage})`}'>
      <center>
        <h1 class="champion_name">{{$route.params.name}}</h1>
        <router-view transition='zoom' class="animated"></router-view>
      </center>
    </div>
</template>

<script>
  import center from '../center.vue';
  export default {
    components: {
      center,
    },
    data() {
      return {
        image: '',
        data: null,
      };
    },
    computed: {
      dDragonBackgroundImage() {
        if (!this.data) return '';
        return `http://ddragon.leagueoflegends.com/cdn/img/champion/splash/${this.data.key}_0.jpg`;
      },
    },
    route: {
      data() {
        this.$http.get(`https://championsuggestor.firebaseio.com/data/${this.$route.params.name}.json`).then((response) => {
          this.data = response.data;
        });
      },
      deactivate() {
        this.data = null;
      },
    },
  };
</script>

<style media="screen">
  .champion-view{
    height: 100%;
    width: 100%;
    background-size: cover;
    background-position: center;
  }
  .champion_name {
    color: white;
    font-size: 3em;
    font-weight: lighter;
  }
</style>



