import auth from '../auth/index.js';
import Firebase from 'firebase';

export const loadingToggled = ({ dispatch }) => {
  dispatch('TOGGLE_LOADING');
};

export const loginAttempted = (store, credentials) => {
  const ref = new Firebase('https://championsuggestor.firebaseio.com');
  store.dispatch('TOGGLE_LOADING');
  ref.authWithPassword({
    email: credentials.email,
    password: credentials.password,
  }, (error) => {
    store.dispatch('TOGGLE_LOADING');
    if (!error) {
      store.dispatch('TOGGLE_LOGIN_STATUS', true);
      store.dispatch('LOGIN_FAIL', false);
    } else {
      store.dispatch('TOGGLE_LOGIN_STATUS', false);
      store.dispatch('LOGIN_FAIL', true);
    }
  });
};

export const loggedOut = ({ dispatch }) => {
  auth.clearSession();
  dispatch('TOGGLE_LOGIN_STATUS', false);
};

export const loginFailed = (store, status) => store.dispatch('LOGIN_FAIL', status);

export const isLoggedIn = (store, status) => store.dispatch('TOGGLE_LOGIN_STATUS', status);

export const redirectUpdated = (store, mutation) => store.dispatch('UPDATE_REDIRECT', mutation);

export const routeUpdated = (store, mutation) => store.dispatch('UPDATE_ROUTE', mutation);



