<template>
  <div class="page-champion-suggest">
    <center>
      <div>
        <template v-for='filter of filterList'>
          <label>
            <input type="checkbox" @click='toggleFilter(filter)'/>
            {{filter.display}}
          </label>
        </template>
      </div>
      <championpreview v-for='champion of filteredChampions' :data='champion' :filters='filterList'></championpreview>
      <router-view transition='zoom' class="animated"></router-view>
    </center>
  </div>
</template>

<script>
  import championpreview from '../championpreview.vue';
  import center from '../center.vue';
  import { loadingToggled } from '../../vuex/actions.js';
  import _ from 'underscore';

  export default {
    vuex: {
      actions: {
        loadingToggled,
      },
    },
    components: {
      championpreview,
      center,
    },
    data() {
      return {
        champions: [],
        filterList: [{
          display: 'airborne',
          key: 'airborne',
        }, {
          display: 'blind',
          key: 'blind',
        }, {
          display: 'forced action',
          key: 'forcedAction',
        }, {
          display: 'nearsight',
          key: 'nearsight',
        }, {
          display: 'root',
          key: 'root',
        }, {
          display: 'silence',
          key: 'silence',
        }, {
          display: 'slow',
          key: 'slow',
        }, {
          display: 'statis',
          key: 'statis',
        }, {
          display: 'stun',
          key: 'stun',
        },
        ],
        filters: [],
        loading: false,
      };
    },
    computed: {
      filteredChampions() {
        return this.champions.filter((o) => {
          for (const filter of this.filters) {
            if (!o[filter]) return false;
          }
          return true;
        });
      },
    },
    methods: {
      toggleFilter(term) {
        const index = this.filters.indexOf(term.key);
        if (index === -1) this.filters.push(term.key);
        else {
          this.filters.$remove(term.key);
        }
      },
    },
    ready() {
      this.loadingToggled();
      this.$http.get('https://championsuggestor.firebaseio.com/data.json').then((response) => {
        this.loadingToggled();
        let temp = Object.entries(response.data);
        temp = temp.map((champion) => {
          const data = champion[1];
          const name = champion[0];
          const rObj = {
            name,
            id: data.id,
            key: data.key,
            role: data.role || false,
          };
          for (const filter of this.filterList) {
            rObj[filter.key] = data[filter.key] || false;
          }
          return rObj;
        });
        this.champions = _.sortBy(temp, (o) => o.name);
      });
    },
  };
</script>

<style media="screen">
  .page-champion-suggest {
    width: 100%;
  }
</style>



