<template>
  <div v-link-active class="container">
    <div class="checkpoint">
      <h2 class="checkpoint_heading">Checkpoint</h2>
      <div class='error animated shake' v-if='loginErrors'>
        Incorrect credentials.
      </div>
      <div class="field">
        <label class='checkpoint_label' for="">Email</label>
        <input v-model='email' type="text" name="email">
      </div>
      <div class="field">
        <label class='checkpoint_label' for="">Password</label>
        <input v-model='password' type="password" name="password">
      </div>
      <button :disabled='inputsAreValid' type="button" name="button" @click='handleLogin()'>login</button>
      <a href='#' @click='close()' class="checkpoint_close">Close</a>
    </div>
  </div>
</template>

<script>
  import { loginAttempted, loginFailed } from '../../vuex/actions.js';
  import { getLoginFail, getLoginStatus, getRedirect } from '../../vuex/getters.js';

  export default {
    vuex: {
      actions: {
        loginAttempted,
        loginFailed,
      },
      getters: {
        loginErrors: getLoginFail,
        loggedIn: getLoginStatus,
        redirect: getRedirect,
      },
    },
    data() {
      return {
        email: '',
        password: '',
      };
    },
    computed: {
      inputsAreValid() {
        return this.email.length === 0 || this.password.length === 0;
      },
    },
    watch: {
      loggedIn(newV) {
        if (newV) {
          this.$router.go({ name: this.redirect || 'home' });
        }
      },
    },
    methods: {
      handleLogin() {
        this.loginAttempted({ email: this.email, password: this.password });
      },
      close() {
        this.loginFailed(false);
        this.$route.router.go(window.history.back() || 'home');
      },
    },
  };
</script>

<style media="screen">
  .container {
    position: fixed;
    top: 25%;
    left: 25%;
    height: 50%;
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center
  }
  .checkpoint {
    color: white;
    background: linear-gradient(135deg, #85368e 0, #2d1f56 40%, #1b1b23 80%);
    box-shadow: 0 0 25px black;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    padding: 15px;
    border-radius: 4px;
    border-bottom: 2px solid #85368e;
  }
  .checkpoint_heading{
    font-size: 2em;
    font-weight: lighter;
    margin: 10px;
  }
  .checkpoint_label {
    margin-right: auto;
  }
  .checkpoint input, button {
    margin: 10px 0;
    padding: 15px;
    min-width: 300px;
    border: none;
    background: #f1f1f1;
    border-radius: 4px;
    border-bottom: 2px solid #c3c3c3;
  }
  .field {
    display: flex;
    flex-direction: column;
    margin: 10px 0;
  }
  .checkpoint button {
    transition: background .2s ease-in;
    color: #CCC2B6;
    padding: 15px;
    background: none;
    border: none;
    border: 2px solid #2ac7a3;
    border-radius: 4px;
  }
  .checkpoint button:disabled{
    color: #CCC2B6;
    border: 2px solid #c72a42;
  }
  .error {
    font-size: 1.3em;
    color: #f64661;
    margin: 10px 0;
  }
.checkpoint .animated {
    -webkit-animation-duration: .5s;
    -moz-animation-duration: .5s;
    animation-duration: .5s;
  }
</style>



