<template>
  <div class="header">
    <center>
      <a v-link='{name: "home"}'>Home</a>
      <a v-link='{name: "user"}'>User</a>
      <a v-if='!loggedIn' @click='handleLogin()'>Login</a>
      <a v-else @click='handleLogout()'>Logout</a>
    </center>
  </div>
</template>

<script>
  import { getLoginStatus, getRoute } from '../vuex/getters.js';
  import { loggedOut } from '../vuex/actions.js';
  import center from './center.vue';

  export default {
    components: {
      center,
    },
    vuex: {
      getters: {
        loggedIn: getLoginStatus,
        toRoute: getRoute,
      },
      actions: {
        loggedOut,
      },
    },
    route: {
      activate() {
        console.log(this.$router);
      },
    },
    ready() {
      console.log(this.$router);
    },
    methods: {
      handleLogin() {
        if (this.toRoute === 'champion') {
          this.$router.go({ name: 'checkpointChampion' });
        } else {
          this.$router.go({ name: 'checkpoint' });
        }
      },
      handleLogout() {
        this.loggedOut();
        this.$router.go({ name: 'home' });
      },
    },
  };
</script>

<style media="screen">
  .header {
    border-top: 2px solid #85368e;
    display: block;
    width: 100%;
    background: linear-gradient(black 0, #191818 82%);
    color: #fff;
  }
  .header-center {
    max-width: 1020px;
    margin: 0 auto;
  }
</style>



