// vue frameworks
import Vue from 'vue';
import VueRouter from 'vue-router';
import VueResource from 'vue-resource';
import lazyload from 'vue-lazyload';

// Vue.config.debug = true;

// transitions
import zoomTransition from './transitions/index.js';
zoomTransition(Vue);

// main router component
import App from './App.vue';

// vue dependencies
Vue.use(VueRouter);
Vue.use(VueResource);
Vue.use(lazyload, {
  loading: 'http://hilongjw.github.io/vue-lazyload/dist/loading-spin.svg',
});

// router setup
export const router = new VueRouter({
  history: true,
  saveScrollPosition: true,
});

// before each route check session
import store from './vuex/store.js';
import { isLoggedIn, routeUpdated } from './vuex/actions.js';
import auth from './auth/index.js';
router.beforeEach((transition) => {
  routeUpdated(store, transition.to.name);
  if (auth.checkAuthenticated()) {
    isLoggedIn(store, true);
  } else isLoggedIn(store, false);
  transition.next();
});

// route mapping object
import Routes from './routes/index';
router.map(Routes);

// start app
router.start(App, '#app');



