export default {
  checkAuthenticated() {
    if (!localStorage.getItem('firebase:session::championsuggestor')) {
      return false;
    }
    return true;
  },
  getUid() {
    return this.parseLocalItem().uid;
  },
  getToken() {
    return this.parseLocalItem().token;
  },
  parseLocalItem() {
    return JSON.parse(localStorage.getItem('firebase:session::championsuggestor'));
  },
  clearSession() {
    localStorage.removeItem('firebase:session::championsuggestor');
  },
};



