<template>
  <div class="page-user">
    <h1>{{first}} {{last}}</h1>
    <h2>{{main}}</h2>
  </div>
</template>

<script>
  import auth from '../../auth/index.js';
  import { loadingToggled, redirectUpdated } from '../../vuex/actions.js';

  export default {
    vuex: {
      actions: {
        loadingToggled,
        redirectUpdated,
      },
    },
    data() {
      return {
        first: '',
        last: '',
        main: '',
      };
    },
    route: {
      activate(transition) {
        if (!auth.checkAuthenticated()) {
          this.redirectUpdated('user');
          transition.redirect('/checkpoint');
        } else {
          this.loadingToggled();
          this.$http.get(`https://championsuggestor.firebaseio.com/users/${auth.getUid()}.json?auth=${auth.getToken()}`).then((response) => {
            this.first = response.data.first;
            this.last = response.data.last;
            this.main = response.data.main;
            this.loadingToggled();
          });
          transition.next();
        }
      },
    },
  };
</script>



