<template>
  <div class="wrapper">
    <app-header></app-header>
    <loader :loading='getLoading' color='#000'></loader>
    <router-view class='router' keep-alive></router-view>
    <app-footer></app-footer>
  </div>
</template>

<script>
  import store from './vuex/store.js' ;
  import { getLoading } from './vuex/getters.js' ;

  import appHeader from './components/header.vue';
  import appFooter from './components/footer.vue';
  import loader from 'vue-spinner/src/RingLoader.vue';

  export default {
    components: {
      appHeader,
      appFooter,
      loader,
    },
    store,
    vuex: {
      getters: {
        getLoading,
      },
    },
  };
</script>

<style media="screen">
  .wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  .router {
    flex: 1;
  }
</style>



