<template>
  <div class="champion-preview">
    <a class='champion-preview_link' v-link='{name: "champion", params: { name : data.name }}' href="#"></a>
    <div class="">
      <img :src='dDragonBackgroundImage' class='champion-preview_image' />
    </div>
    <div class="champion-preview_container">
      <div class='champion-preview_name'>{{data.name}}</div>
      <div v-if='data[filter.key]' v-for='filter of filters' :class="{[filter.key]: true, 'champion-preview_tag': true}">
        {{filter.display}}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['data', 'filters'],
  computed: {
    dDragonBackgroundImage() {
      return `http://ddragon.leagueoflegends.com/cdn/6.9.1/img/champion/${this.data.key}.png`;
    },
  },
};
</script>

<style media="screen">
  .champion-preview {
    float: left;
    width: calc((100% / 3) - 2%);
    margin: 1%;
    position: relative;
    transition: ease-in .15s;
    display: flex;
    flex-direction: row;
    background: #1b1b23;
    border-bottom: 3px solid #85368e;
  }
  .champion-preview:hover {
    border-bottom: 3px solid #db22f1;
  }
  .champion-preview_link {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .champion-preview_image {
    background-color: #383838;
    display: block;
    margin: 0 auto;
    width: 120px;
    height: 120px;
    background-size: cover;
    background-position: top center;
    background-repeat: no-repeat;
  }
  .champion-preview_image[lazy=loading] {
    background-size: auto;
    background-position: center;
  }
  .champion-preview_name{
    padding: 2px 10px;
    color: white;
    flex: 0 0 100%;
  }
  .champion-preview_container {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
  }
  .champion-preview_tag{
    padding: 2px 10px;
    border-radius: 4px;
    height: auto;
    margin: 0 5px;
    font-size: 1em;
    background: #000;
    box-shadow: 0 1px 1px black;
    color: white;
  }
  .champion-preview_tag--slow{
    background: #cf8353;
    color: #6b3b1c
  }
  .champion-preview_tag--stun{
    background: #fff85c;
    color: #706c0d;
  }
  .champion-preview_tag--knockup{
    background: #2b93f4;
    color: #0f3e6a;
  }
</style>



